import React, { Component } from 'react';
import { Button } from 'antd';
// import './style.scss';

export default class BtnCopy extends Component {
  states = '';

  render() {
    return (
      <Button
        {...this.props}
        className={`btnAction btnCopy ${this.props.className}`}
        // icon="search"
        size="large"
      >
        {this.props.title}
      </Button>
    );
  }
}

BtnCopy.defaultProps = {
  className: '',
  title: 'Sao chp',
};



