import React, { Component } from 'react';
import { Row, Col, Form, Popconfirm, Input } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate/index';
import { BtnSearch, BtnClear } from 'components/common/button';
import { getYearEducations } from 'services/yearEducationService';
import { getSemesters } from 'services/semesterService';
import { SelectAjax } from 'components/common/index';
import { getPolicyObject, getFeeObject } from 'services/StudentReduction';
import { udpateQueryParams, getQueryParams } from 'utils/urlUtil';
import BtnCopy from './BtnCopy';
import './style.scss';

@Form.create({ name: 'FormSearchClasses' })
class FormSearchStudent extends Component {
  componentDidMount() {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  }
  getSubjectTypes = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'A',
        },
        {
          id: '2',
          label: 'B',
        },
        {
          id: '3',
          label: 'C',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 3,
    };
    const res = {
      data,
    };
    return res;
  };
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      this.props.dataForm(values);
      udpateQueryParams(values);
    });
  };

  render() {
    const { getFieldDecorator, getFieldValue, setFieldsValue } = this.props.form;

    const yearEduId = getFieldValue('yearEduId');
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate required form={this.props.form} />
          <Col span={9}>
            <Form.Item label="Nm hc">
              {getFieldDecorator('yearEduId', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lng nhp thng tin!',
                  },
                ],
              })(
                <SelectAjax
                  service={getYearEducations}
                  setLabel={yearEdu => `${yearEdu.fromYear}-${yearEdu.toYear}`}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={9}>
            <Form.Item label="Hc k">
              {getFieldDecorator('semesterId', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lng nhp thng tin!',
                  },
                ],
              })(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  service={getSemesters}
                  params={{ yearEduId }}
                  allowGetData={!!yearEduId}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="i tng CS">
              {getFieldDecorator('policyObject', {})(<SelectAjax service={getPolicyObject} />)}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="i tng hc ph">
              {getFieldDecorator('fOId', {})(<SelectAjax service={getFeeObject} />)}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="H tn">{getFieldDecorator('fullname')(<Input />)}</Form.Item>
          </Col>
          <Col xs={24} sm={24} md={24}>
            <Popconfirm
              title="Bn mun sao chp sang k sau?"
              onConfirm={this.handleCopyToNextSemedter}
              // onCancel={cancel}
              okText="ng"
              cancelText="Hy"
            >
              <BtnCopy title="Sao chp sang k sau" style={{ margin: '5px 0' }} />
            </Popconfirm>
            <div className="btn-group" style={{ float: 'right' }}>
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormSearchStudent;



