import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnExport from 'components/common/button/BtnExport';
import { Card, Row, Col, Table, Button, message } from 'antd';
import { TableData } from 'components/common';
import {
  getStudentReduction,
  getStudentReductionBySemester,
  deleteStudentReduction,
  addStudentReduction,
  downloadFileStudentReduction,
} from 'services/StudentReduction';
import { getQueryParams } from 'utils/urlUtil';
import FormSearchStudent from './FormSearchStudent';
import FormConfigValues from './FormConfigValues';
import './style.scss';

class TuitionReductionPage extends Component {
  state = {
    reload: 0,
    paramSearchClass: {},
    addingStudents: {},
    addingStudentsTableData: {},
    deletingStudents: {},
    selectedForRemoveRowKeys: [],
    dataFormNew: [],
    dataTableReduction: [],
    loading: false,
  };

  componentDidMount() {
    this.handleChangeQueryParams();
  }

  componentWillReceiveProps() {
    this.handleChangeQueryParams();
  }

  onSelectForAddStudent = (selectedStudent, isSelect) => {
    const addingStudents = { ...this.state.addingStudents };
    if (isSelect) {
      addingStudents[selectedStudent.studentId] = selectedStudent;
    } else {
      delete addingStudents[selectedStudent.studentId];
    }
    this.setState({ addingStudents });
  };

  onSelectAllForAddStudent = (selectedStudents, isSelect) => {
    if (isSelect) {
      const addingStudents = {};
      selectedStudents.forEach(student => {
        addingStudents[student.studentId] = student;
      });
      this.setState({ addingStudents });
    } else {
      this.setState({ addingStudents: {} });
    }
  };

  onSelectForRemoveStudent = (selectedStudent, isSelect) => {
    const deletingStudents = { ...this.state.deletingStudents };
    if (isSelect) {
      deletingStudents[selectedStudent.studentId] = selectedStudent;
    } else {
      delete deletingStudents[selectedStudent.studentId];
    }
    this.setState({
      deletingStudents,
    });
  };

  onSelectAllForRemoveStudent = (selectedStudents, isSelect) => {
    if (isSelect) {
      const deletingStudents = {};
      selectedStudents.forEach(student => {
        deletingStudents[student.studentId] = student;
      });
      this.setState({ deletingStudents });
    } else {
      this.setState({ deletingStudents: {} });
    }
  };

  onAddStudents = async () => {
    const addingStudentsTableData = { ...this.state.addingStudentsTableData };
    const { addingStudents, dataFormNew, dataTableReduction } = this.state;
    const listAddnew = [];
    Object.values(addingStudents).forEach(item => {
      const addNewDate = {
        studentId: item.studentId,
        semesterId: dataFormNew.semesterId,
        yearEduId: dataFormNew.yearEduId,
        foId: item.feeObjectId,
        receivableId: dataFormNew.receivableId,
        reducedPercent: parseInt(dataFormNew.reducePercent),
        reducedAmount: dataFormNew.reduceMoney,
      };
      const dataTest = dataTableReduction.findIndex(
        student => student.studentId === item.studentId
      );
      if (dataTest === -1) {
        listAddnew.push(addNewDate);
      }
    });
    this.setState({ loading: true });
    try {
      await addStudentReduction(listAddnew);
      this.setState({ selectedRowKeysStudent: [], addingStudents: [], loading: false });
      message.success('Thm mi thnh cng');
      this.dataFormAdd(dataFormNew);

      this.setState({ addingStudentsTableData });
    } catch (error) {
      this.setState({ loading: false });
      console.log(error);
    }
  };

  onRemoveStudents = async () => {
    const { deletingStudents, dataFormNew } = this.state;
    const listIdDelete = [];
    Object.values(deletingStudents).forEach(item => {
      const studentid = { studentId: item.studentId };
      listIdDelete.push(studentid);
    });
    this.setState({
      loading: true,
    });
    try {
      await deleteStudentReduction(listIdDelete);

      this.setState({ selectedForRemoveRowKeys: [], deletingStudents: [], loading: false });
      message.success('Xo thnh cng');
      this.dataFormAdd(dataFormNew);
    } catch (error) {
      this.setState({ loading: false });
    }
  };

  handleChangeQueryParams = () => {
    this.setState({ paramSearchClass: getQueryParams() });
  };

  dataFormSeach = data => {
    this.setState({
      paramSearchClass: data,
    });
  };

  formSearch = data => {
    this.setState({
      dataFormNew: data,
    });
  };

  dataFormAdd = async data => {
    const { paramSearchClass } = this.state;
    const dataFromSeacrch = {
      ...data,
      yearEduId: paramSearchClass.yearEduId,
      semesterId: paramSearchClass.semesterId,
    };
    this.formSearch(dataFromSeacrch);
    const dataTableReduction = await getStudentReductionBySemester(dataFromSeacrch);
    this.setState({
      dataTableReduction: dataTableReduction.data.items,
    });
  };

  exportExcel = () => {
    const { dataFormNew } = this.state;
    downloadFileStudentReduction(dataFormNew)
      .then()
      .catch();
    // message.error(error.response.data.reason);
  };

  render() {
    const {
      reload,
      paramSearchClass,
      addingStudents,
      addingStudentsTableData,
      deletingStudents,
      selectedForRemoveRowKeys,
      selectedRowKeysStudent,
      dataTableReduction,
      loading,
    } = this.state;
    const isBtnAddVisible = addingStudents ? Object.entries(addingStudents).length < 1 : false;
    const isBtnDeleteVisible = deletingStudents
      ? Object.entries(deletingStudents).length < 1
      : false;
    const isExportAndPrintBtnDisable = addingStudentsTableData
      ? Object.entries(addingStudentsTableData).length < 1
      : false;

    const isExportExcelBtnDisable = dataTableReduction.length > 0;
    const columns = [
      {
        title: 'M SV',
        dataIndex: 'studentCode',
        key: 'studentCode',
      },
      {
        title: 'H v tn',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Ngy sinh',
        dataIndex: 'dob',
        key: 'dob',
      },
      {
        title: 'Gii tnh',
        dataIndex: 'gender',
        key: 'gender',
      },
      {
        title: 'Tn lp',
        dataIndex: 'class',
        key: 'class',
      },
      {
        title: 'i tng chnh sch',
        dataIndex: 'policyObject',
        key: 'policyObject',
      },
      {
        title: 'i tng hc ph',
        dataIndex: 'feeObject',
        key: 'feeObject',
      },
    ];
    const selectedStudentColumns = [
      {
        title: 'M SV',
        dataIndex: 'studentCode',
        key: 'studentCode',
      },
      {
        title: 'H v tn',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Ngy sinh',
        dataIndex: 'dob',
        key: 'dob',
      },
      {
        title: 'Gii tnh',
        dataIndex: 'gender',
        key: 'gender',
      },
      {
        title: 'Tn lp',
        dataIndex: 'class',
        key: 'class',
      },
      {
        title: 'Tn i tng',
        dataIndex: 'feeObject',
        key: 'feeObject',
      },
      {
        title: '% min gim',
        dataIndex: 'reducedPercent',
        key: 'reducedPercent',
      },
      {
        title: 'S tin gim',
        dataIndex: 'reducedAmount',
        key: 'reducedAmount',
      },
    ];
    const rowSelection = {
      selectedRowKeys: selectedRowKeysStudent,
      onSelect: (record, selected) => this.onSelectForAddStudent(record, selected),
      onSelectAll: (selected, selectedRows) =>
        this.onSelectAllForAddStudent(selectedRows, selected),
      onChange: selectedRowKeys => {
        this.setState({ selectedRowKeysStudent: selectedRowKeys });
      },
    };
    const selectedTableRowSelection = {
      selectedRowKeys: selectedForRemoveRowKeys,
      onSelect: (record, selected) => this.onSelectForRemoveStudent(record, selected),
      onSelectAll: (selected, selectedRows) =>
        this.onSelectAllForRemoveStudent(selectedRows, selected),
      onChange: selectedRowKeys => {
        this.setState({ selectedForRemoveRowKeys: selectedRowKeys });
      },
    };

    return (
      <Card title="Lp danh sch min gim hc ph v l ph">
        <Row>
          <Col span={24}>
            <FormSearchStudent dataForm={this.dataFormSeach} />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              formSearch
              service={getStudentReduction}
              paramSearch={paramSearchClass}
              columns={columns}
              rowSelection={rowSelection}
              rowKey="id"
            />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <div className="btn-group">
              <Button
                className="btnAction btnAdd"
                size="large"
                icon="plus-circle"
                onClick={this.onAddStudents}
                disabled={isBtnAddVisible}
              >
                Thm SV min gim
              </Button>
              <Button
                className="btnAction btnDelete"
                size="large"
                icon="delete"
                onClick={this.onRemoveStudents}
                disabled={isBtnDeleteVisible}
              >
                Xa SV min gim
              </Button>
              <BtnExport
                className="btnExport"
                title="Xut Excel"
                disabled={!isExportExcelBtnDisable}
                onClick={() => this.exportExcel()}
              />
              <Button
                className="btnAction btnPrint"
                icon="printer"
                size="large"
                disabled={isExportAndPrintBtnDisable}
              >
                In
              </Button>
            </div>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <FormConfigValues formValue={this.formSearch} dataFormConfigValues={this.dataFormAdd} />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table
              loading={loading}
              dataSource={dataTableReduction || []}
              columns={selectedStudentColumns}
              rowSelection={selectedTableRowSelection}
            />
            ;
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <TuitionReductionPage />
  </Page>
);



